import React from "react";
import styled from 'react-emotion';



const Label = styled('label')`
  display: inline-block;
  float: left;
  clear: left;
  width: 100px;
  text-align: left;
  margin-bottom: 1em;
`;

const Input = styled('input')`
  display: inline-block;
  float: left;
  width: 20vw;
`;

const TextArea = styled('textarea')`
  display: inline-block;
  float: left;
  width: 20vw;
  height: 20vw;
`;


export default function Comments({
  data
}) {
  return (
    <div style={{ display: "inline-block", width: "60vw" }}>
      <h2>Leave a comment</h2>
      <form method="POST" action="https://api.staticman.net/v2/entry/bryanwb/bryanwb.github.io/master/comments">
        <input name="options[redirect]" type="hidden" value="https://hotair.tech" />
        <input name="options[slug]" type="hidden" value="{{ data.post.title }}" />
        <Label>Name</Label><Input name="fields[name]" type="text" />
        <Label>E-mail</Label><Input name="fields[email]" type="email" />
        <Label>Message</Label><TextArea name="fields[message]" ></TextArea>
        <Label>
          <button styled={{ float: 'left', clear: 'right' }} type="submit">Send</button>
        </Label>
      </form>
    </div>
  )
};
